<template>
 <div class="preview-course">
    <title-curmb mate="大纲预览"></title-curmb>
    <div class="preview-course_head">
      {{refInfo.courseName}}
      <span @click="back"><SvgIcon icon-class="iconfanhui" size="14px"></SvgIcon>&nbsp;&nbsp;返回</span>
    </div>
    <div class="preview-course_body">
      <h4>基本信息</h4>
      <ul class="basic-information">
        <li>
          <span>课程代码：&nbsp;&nbsp;</span>
          <label>{{refInfo.courseCode}}</label>
        </li>
         <li>
          <span>课程名称：&nbsp;&nbsp;</span>
          <label>{{refInfo.courseName}}</label>
        </li>
         <li>
          <span>英文名称：&nbsp;&nbsp;</span>
          <label>{{refInfo.enName}}</label>
        </li>
         <li>
          <span>学     时：&nbsp;&nbsp;</span>
          <label>{{refInfo.period}}</label>
        </li>
         <li>
          <span>学     分：&nbsp;&nbsp;</span>
          <label>{{refInfo.credit}}</label>
        </li>
         <li>
          <span>课程性质：&nbsp;&nbsp;</span>
          <label>{{refInfo.className}}</label>
        </li>
         <li>
          <span>适用专业：&nbsp;&nbsp;</span>
          <label>{{refInfo.specialName}}</label>
        </li>
          <li>
          <span>先修课程：&nbsp;&nbsp;</span>
          <label>{{refInfo.frontCourseId}}</label>
        </li>    
      </ul>
      <h4 class="hActive" style="margin-top:30px">课程性质</h4>
      <p  class="nature-content">{{refInfo.remarks}}</p>
      <h4 class="hActive">课程目标</h4>
      <ul class="objective">
        <li v-for="(vo, index) in targetViewGoals" :key="index">
          <span>{{vo.name}}：&nbsp;&nbsp;</span>
          <label>{{vo.content}}</label>
        </li>
      </ul>
      <h4 class="hActive">课程目标与毕业要求指标点的对应关系</h4>
      <el-table :data="targetViewPoints" border class="cc-table" style="width: 100%;">
          <el-table-column prop="claimName" label="毕业要求"></el-table-column>
          <el-table-column prop="pointContent" label="毕业要求指标点"></el-table-column>
          <el-table-column  label="课程目标">
               <template slot-scope="scope">
                 <span v-for="(item, index) in scope.row.goal" :key="index" style="margin-right:5px">{{item.courseGoalName}}</span>
             </template>
          </el-table-column>
      </el-table>
      <h4 class="hActive">教学内容与基本要求</h4>
      <el-table :data="contentView" border class="cc-table" style="width: 100%;">
          <el-table-column type="index" label="序号"></el-table-column>
          <el-table-column prop="courseContent" label="教学内容（知识点）"></el-table-column>
          <el-table-column prop="courseRequirment" label="教学要求"></el-table-column>
          <el-table-column prop="period" label="学时"></el-table-column>
          <el-table-column prop="courseMode" label="教学方式"></el-table-column>
          <el-table-column  label="课程目标">
             <template slot-scope="scope">
                 <span v-for="(item, index) in scope.row.contentGolds" :key="index" style="margin-right:5px">{{item.courseGoalName}}</span>
             </template>
          </el-table-column>
      </el-table>
      <h4 class="hActive">课程考核与评价方式</h4>
       <ul class="way">
        <li v-for="(vo, index) in examList" :key="index">{{vo.examine_name}}: {{vo.ratio}}</li>
      </ul>
      <h4 class="hActive">考核与评价方式及成绩比例</h4>
      <el-table v-if="loadding" :data="pointCourseGoals" border class="cc-table"  style="width: 100%;">
        <el-table-column prop="goalName" label="课程目标"></el-table-column>
        <el-table-column prop="pointCodeContent" label="毕业要求指标点"></el-table-column>
        <el-table-column v-for="(item, index) in pointCourseGoals[0].examList" :key="index" > 
            <template slot="header">
                  {{item.title || ''}}
            </template>
            <template slot-scope="scope">
                 {{scope.row[`scale_${index}`]}}
            </template>
        </el-table-column>
      </el-table>
      <el-row v-for="(item, index) in examRequireView" :key="index">
            <h4 class="hActive">{{`“${item.examine_name}”考核与评价标准`}}</h4>
            <el-table :data="item.requires" border class="cc-table" style="width: 100%;">
                <el-table-column label="基本要求" prop="basicRequire"></el-table-column>
                <el-table-column label="优秀" prop="excellence"></el-table-column>
                <el-table-column label="良好" prop="well"> </el-table-column>
                <el-table-column label="合格" prop="qualified"></el-table-column>
                <el-table-column label="不合格" prop="disqualification"></el-table-column>
                <el-table-column label="成绩比例（%）"  prop="ratio"></el-table-column>
            </el-table>
      </el-row>
      <h4 class="hActive">教材及参考书目</h4>
      <p class="nature-content">{{booklist}}</p>
      <h4 class="hActive">备注说明</h4>
       <ul class="objective">
        <li>
          <span>课程达成评价：&nbsp;&nbsp;</span>
          <label>{{explains}}</label>
        </li>
        <li>
          <span>说明：&nbsp;&nbsp;</span>
          <label>本大纲在执行过程中可根据具体情况，对教学内容及顺序安排进行调整。</label>
        </li>
      </ul>
    </div>
 </div>
</template>

<script>
import TitleCurmb from './components/TitleCurmb'
export default {
 name: 'PreviewCourse',
 components: {TitleCurmb},
 data() {
 return {
   loadding:false,
   refInfo:{}, 
   targetViewGoals:[],
   targetViewPoints:[],
   contentView:[],
   examList:[],
   pointCourseGoals:[],
   examRequireView:[],
   booklist:'',
   explains:'',
   api:{
        details:courseId=>`/basic/course/previewOutline/${courseId}`
    }
 }
 },
 created(){},
 mounted(){
   this.initData()
 },
 methods:{
    initData(){
        const url=this.api.details(this.$route.query.id)
        this.$get(url).then(res =>{
            if(res.status ===1){     
              this.refInfo=res.data.refInfo
              this.targetViewGoals=res.data.targetView.goals || []
              this.targetViewPoints=res.data.targetView.points || []
              this.contentView=res.data.contentView || []
              this.examList=res.data.examPattenView.examList || []
              this.pointCourseGoals=res.data.examPattenView.pointCourseGoals || []
              this.examRequireView=res.data.examRequireView || []
              this.booklist=res.data.courseBooklistView.booklist
              this.explains=res.data.courseBooklistView.explains
              const examArr=JSON.parse(JSON.stringify(this.examList))
              this.pointCourseGoals.forEach((item, index) =>{
                    item.pointCodeContent=`支撑毕业要求指标点${item.pointCode || ''}`
                    item.examList=[]
                    //接口返回不为空的情况
                    if(item.ratios && item.ratios.length){
                        item.ratios.forEach(v =>{
                            examArr.forEach((vs, num) =>{
                                if(v.examId === vs.examine_id){
                                    item[`scale_${num}`]=v.value
                                }
                            })
                        
                        })
                    } 
                    examArr.forEach((vo, n) =>{
                        vo.title=`${vo.examine_name || ''}(${vo.ratio || ''})`
                        //接口返回为空的情况
                        if(!item.ratios || item.ratios.length ===0){
                            item[`scale_${n}`]=item[`scale_${n}`] || ''
                        }   
                        item.examList.push(vo)

                        })
                })

                if(this.pointCourseGoals.length){
                       this.loadding=true;
                }
               
             
            }else{
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
            }
        })
     },
   back(){
     this.$router.push({
       path:'/Construction/CourseConstruction'
     })
   }
 }
}
</script>

<style lang='scss' scoped>
.preview-course{
  height:100%;
  .preview-course_head{
     height:86px;
     line-height:86px;
     text-align:center;
     background:rgba(3,177,135,1);
     border-radius:10px 10px 0px 0px;
     font-size:24px;
     font-weight:bold;
     color:#fff;
     position:relative;
     span{
       display:inline-block;
       width:90px;
       height:28px;
       line-height:28px;
       text-align:center;
       border:1px solid rgba(176,249,224,1);
       border-radius:14px;
       font-size:14px;
       color:#B0F9E0;
       font-weight:500;
       cursor:pointer;
       position:absolute;
       left:30px;
       top:29px;
     }
  }
  .preview-course_body{
    background:#fff;
    box-sizing:border-box;
    padding:30px;
    h4{
      font-size:20px;
      color:#323232;
    }
    .hActive{
      margin:50px 0 30px 0;
    }
    .basic-information{
       display:flex;
       flex-wrap: wrap;
       margin-top:14px;
       padding:0 15px;
       box-sizing:border-box;
       li{
         font-size:14px;
         display:flex;
         min-width: 290px;
         margin-bottom:20px;
         span{
           color:#696969;
         }
         label{
            color:#969696;
         }
       }
    }
    .nature-content{
      padding:0 15px;
      font-size:14px;
      line-height:24px;
      color:#969696;
    }
    .objective{
       display:flex;
       flex-direction:column;
       padding:0 15px;
       box-sizing:border-box;
        li{
         font-size:14px;
         display:flex;
         margin-bottom:30px;
         span{
           color:#323232;
         }
         label{
            color:#969696;
         }
         &:last-child{
           margin-bottom:0;
         }
       }
    }
    .way{
       display:flex;
       flex-direction:column;
       padding:0 15px;
       box-sizing:border-box;
       li{
          font-size:14px;
          color:#323232;
          margin-bottom:30px;
          &:last-child{
            margin-bottom:0;
          }
       }
    }
  }
}
</style>



